<template>
  <div>
    <div class="px-3">
      <v-text-field
        v-model="search"
        append-icon="fa fa-search"
        label="Search"
        single-line
        hide-details
        class="mb-3"
      ></v-text-field>
    </div>
    <v-data-table
      :headers="historicalDataTableHeaders"
      :items="this.personsFiltered"
      :search="search"
      :expanded.sync="expanded"
      show-expand
      single-expand
      item-key="name"
      dense
      :item-class="row_classes"
      class="elevation-1"
    >
      <template v-slot:expanded-item="{ headers, item }">
        <td
          :colspan="headers.length"
          v-bind:class="{'red accent-4':checkIfCovidEr(item), 'success': checkIfCleared(item)}"
        >
          <v-simple-table
            border="1"
            dense
            v-bind:class="{'red accent-4 pt-4':checkIfCovidEr(item), 'success pt-4': checkIfCleared(item)}"
          >
            <template v-slot:default>
              <tbody>
                <tr>
                  <th colspan="2" class="text-center">OTHER INFORMATION</th>
                </tr>
                <tr>
                  <th width="50%" class="text-center">Symptoms and History</th>
                  <td class="text-center">{{item.symptomsAndHistory}}</td>
                </tr>
                <tr>
                  <th width="50%" class="text-center">Temperature</th>
                  <td class="text-center">{{item.temperature}}</td>
                </tr>
                <tr>
                  <th class="text-center">Birthdate</th>
                  <td class="text-center">{{item.birthdate}}</td>
                </tr>
                <tr>
                  <th class="text-center">Age</th>
                  <td class="text-center">{{item.age}}</td>
                </tr>
                <tr>
                  <th class="text-center">Gender</th>
                  <td class="text-center">{{item.gender}}</td>
                </tr>
                <tr>
                  <th class="text-center">Department</th>
                  <td class="text-center">{{item.department}}</td>
                </tr>
                <tr>
                  <th class="text-center">Position</th>
                  <td class="text-center">{{item.position}}</td>
                </tr>
                <tr>
                  <th class="text-center">Class</th>
                  <td class="text-center">{{item.class}}</td>
                </tr>
                <tr>
                  <td colspan="2">&nbsp;</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </td>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  props: ["persons", "showClearButton", "userType"],
  data() {
    return {
      search: "",
      expanded: [],
      personsFiltered: [],
      historicalDataTableHeaders: [
        {
          text: "Code",
          value: "code",
        },
        {
          text: "Name",
          value: "name",
        },
        {
          text: "Mobile #",
          value: "mobileNo",
        },
        {
          text: "ETriage Date Declared and CIF Trans Date",
          value: "etriage_datetime",
        },
        {
          text: "Disposition",
          value: "disposition",
        },
        {
          text: "Classification",
          value: "classification",
        },
        // {
        //   text: "Department",
        //   value: "department",
        // },
        // {
        //   text: "Temperature",
        //   value: "temperature",
        // },
        // {
        //   text: "Symptoms & History",
        //   value: "symptomsAndHistory",
        // },
      ],
    };
  },
  computed: {
    ...mapGetters(["apiUrl", "apiKey"])
  },
  methods: {
    checkIfCovidEr(employee) {
      if (this.viewType == "infirmary") {
        return false;
      }
      if (employee.isCleared) {
        return false;
      }
      if (employee.isForCovidEr == 1) {
        return true;
      }
      return false;
    },
    checkIfCleared(employee) {
      if (employee.isCleared) {
        return true;
      }
    },
    row_classes(item) {
      if (this.viewType == "infirmary") {
        return false;
      }
      if (item.isCleared) {
        return "success";
      }
      if (item.isForCovidEr == 1) {
        return "red accent-4";
      }
      return false;
    },
    async clearEmployee(id, clearPastData = true) {
      const formData = {
        id: id,
        isPastData: clearPastData,
        user: "7679",
      };
      const response = await fetch(
        `${this.apiUrl}etriage/clear-employee?auth=${this.apiKey}`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(formData),
        }
      );
      const responseJson = await response.json();
      if (responseJson.error) {
        this.formErrorMessage = responseJson.message;
        return;
      }
      this.personsFiltered.find((person) => person.id == id).isCleared = true;
    },
  },
  created() {
    this.personsFiltered = this.persons;
  },
};
</script>

<style>
.v-application .text-start {
  text-align: center !important;
}
.v-data-table-header th {
  white-space: nowrap;
}
</style>