<template>
  <v-container>
    <div class="display-1 text-center mt-5">
      UERM E-Triage Dashboard
      <b>Employees</b>
    </div>
    <v-menu
      ref="menu"
      v-model="menu"
      :close-on-content-click="false"
      :return-value.sync="date"
      transition="scale-transition"
      offset-y
      min-width="290px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
          v-model="date"
          label="Date"
          prepend-icon="fas fa-calendar"
          readonly
          v-bind="attrs"
          v-on="on"
        ></v-text-field>
      </template>
      <v-date-picker v-model="date" no-title scrollable>
        <v-spacer></v-spacer>
        <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
        <v-btn text color="primary" @click="changeDate">OK</v-btn>
      </v-date-picker>
    </v-menu>
    <div class="text-center ma-12">
      <v-progress-circular
        v-if="cardFormLoading"
        :size="150"
        class="mx-3"
        indeterminate
        color="primary"
      ></v-progress-circular>
    </div>
    <div class="d-flex flex-wrap mt-5" v-if="!cardFormLoading">
      <v-row>
        <v-col>
          <Card :header="'Employees Today'" class="primary" :employees="eTriageEmployeesCount" :date="date" :userType="'employees'" />
        </v-col>
        <v-col>
          <Card :header="'For COVID ER'" class="error" :employees="eTriageEREmployees" :date="date" :userType="'employees'" />
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <Card :header="'Male'" class="success" :employees="eTriageMaleEmployees" :date="date" :userType="'employees'" />
        </v-col>
        <v-col>
          <Card :header="'Female'" class="pink" :employees="eTriageFemaleEmployees" :date="date" :userType="'employees'" />
        </v-col>
        <v-col>
          <Card :header="'Undisclosed Gender'" class="grey" :employees="eTriageUndisclosedGender" :date="date" :userType="'employees'" />
        </v-col>
      </v-row>
      
      <v-row>
        <v-col>
          <Card :header="'Officer'" class="purple" :employees="eTriageOfficerClass" :date="date" :userType="'employees'" />
        </v-col>
        <v-col>
          <Card :header="'Management'" class="purple" :employees="eTriageManagementClass" :date="date" :userType="'employees'" />
        </v-col>
        <v-col>
          <Card :header="'Rank and File'" class="purple" :employees="eTriageRankAndFileClass" :date="date" :userType="'employees'" />
        </v-col>
        <v-col>
          <Card :header="'Faculty'" class="purple" :employees="eTriageFacultyClass" :date="date" :userType="'employees'" />
        </v-col>
        <v-col>
          <Card :header="'Residents/PGI'" class="purple" :employees="eTriageResidentPGIClass" :date="date" :userType="'employees'" />
        </v-col>
      </v-row>

      <v-row>
        <v-col>
          <Card :header="'Age Group 20 Below'" class="cyan" :employees="eTriageToday20Below" :date="date" :userType="'employees'" />
        </v-col>

        <v-col>
          <Card :header="'Age Group 20 to 30'" class="cyan" :employees="eTriageToday20to30" :date="date" :userType="'employees'" />
        </v-col>

        <v-col>
          <Card :header="'Age Group 30 to 40'" class="cyan" :employees="eTriageToday30to40" :date="date" :userType="'employees'" />
        </v-col>

        <v-col>
          <Card :header="'Age Group 40 to 50'" class="cyan" :employees="eTriageToday40to50" :date="date" :userType="'employees'" />
        </v-col>

        <v-col>
          <Card :header="'Age Group 50 to 60'" class="cyan" :employees="eTriageToday50to60" :date="date" :userType="'employees'" />
        </v-col>

        <v-col>
          <Card :header="'Age Group 60 Above'" class="cyan" :employees="eTriageToday60Above" :date="date" :userType="'employees'" />
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <v-card
            class="mx-auto d-flex text-center align-center justify-center pa-3"
            color="blue-grey"
            dark
          >
            <v-list-item three-line>
              <v-list-item-content>
                <v-list-item-title color="primary" block dark>
                  <div class="headline mb-1 text-center">DTR without ETriage</div>
                </v-list-item-title>
                <v-list-item-action-text class="py-3">
                  <div
                    class="headline text-center pt-2"
                    style="color:white"
                  >{{dtrWithoutTriage.length}}</div>
                </v-list-item-action-text>
                <v-progress-circular v-if="dtrFormLoading" class="mx-3" indeterminate color="white"></v-progress-circular>
                <v-list-item-action-text
                  class="text-center pt-3"
                  style="color: white; font-size: 16px"
                  v-if="!dtrFormLoading && dtrWithoutTriage == 0"
                >
                  <v-btn @click="getDTRWithoutTriage">Show details</v-btn>
                </v-list-item-action-text>
                <Detailed v-bind:persons="dtrWithoutTriage" v-if="dtrWithoutTriage.length > 0" />
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>

        <v-col cols="12">
          <v-card class="mx-auto d-flex align-center justify-center pa-3" color="red accent-4" dark>
            <v-list-item three-line>
              <v-list-item-content>
                <v-list-item-title color="primary" block dark>
                  <div class="headline mb-1 text-center" color="primary">ETriage CIF</div>
                </v-list-item-title>
                <v-list-item-action-text class="py-3">
                  <div
                    class="headline text-center pt-2"
                    style="color:white"
                  >{{eTriageCIF.length}}</div>
                </v-list-item-action-text>
                <v-dialog v-model="isEtriageCIFShown">
                  <v-card dark>
                    <div class="text-center mt-10">
                      <v-progress-circular v-if="formLoading" class="mx-3 mt-10" indeterminate color="white"></v-progress-circular>
                    </div>
                    <EtriageCIF v-bind:persons="eTriageCIF" v-bind:userType="'employees'" v-if="eTriageCIF.length > 0 && !formLoading" />
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="white" outlined text @click="isEtriageCIFShown = false">Close</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-list-item-action-text
                  class="text-center pt-3"
                  style="color: white; font-size: 16px"
                >
                  <v-btn @click="getEtriageCIF">Show Details</v-btn>
                </v-list-item-action-text>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
        
        <v-col cols="12">
          <v-card class="mx-auto d-flex align-center justify-center pa-3" color="warning" dark>
            <v-list-item three-line>
              <v-list-item-content>
                <v-list-item-title color="primary" block dark>
                  <div class="headline mb-1 text-center" color="primary">Historical Data</div>
                </v-list-item-title>
                <v-list-item-action-text class="py-3">
                  <div
                    class="headline text-center pt-2"
                    style="color:white"
                  >{{eTriageHistorical.length}}</div>
                </v-list-item-action-text>
                <v-dialog v-model="isDetailsShown">
                  <v-card dark>
                    <div class="text-center mt-10">
                      <v-progress-circular v-if="formLoading" class="mx-3 mt-10" indeterminate color="white"></v-progress-circular>
                    </div>
                    <Detailed v-bind:persons="eTriageHistorical" v-bind:userType="'employees'" v-if="eTriageHistorical.length > 0 && !formLoading" />
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="white" outlined text @click="isDetailsShown = false">Close</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-list-item-action-text
                  class="text-center pt-3"
                  style="color: white; font-size: 16px"
                >
                  <v-btn @click="getHistorical">Show Historical</v-btn>
                </v-list-item-action-text>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
import Detailed from "../components/tables/Detailed";
import EtriageCIF from "../components/tables/EtriageCIF";
import Card from "../components/core/Card";
export default {
  components: {
    Detailed,
    EtriageCIF,
    Card,
  },
  data() {
    return {
      search: "",
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      isDetailsShown: false,
      isEtriageCIFShown: false,
    };
  },
  computed: {
    ...mapGetters([
      "formLoading",
      "cardFormLoading",
      "dtrFormLoading",
      "eTriageEmployeesCount",
      "eTriageEREmployees",
      "eTriageMaleEmployees",
      "eTriageFemaleEmployees",
      "eTriageUndisclosedGender",
      "eTriageManagementClass",
      "eTriageRankAndFileClass",
      "eTriageOfficerClass",
      "eTriageResidentPGIClass",
      "eTriageFacultyClass",
      "eTriageToday20Below",
      "eTriageToday20to30",
      "eTriageToday30to40",
      "eTriageToday40to50",
      "eTriageToday50to60",
      "eTriageToday60Above",
      "eTriageHistorical",
      "eTriageCIF",
      "dtrWithoutTriage",
    ]),
  },
  mounted() {
    if (!localStorage.isLoggedIn) {
      this.$router.push("/");
    }
    this.getETriageToday();
  },
  methods: {
    async changeDate(){
      this.$refs.menu.save(this.date)
      await this.getETriageToday();
    },
    async getETriageToday() {
      await this.$store.dispatch("getETriageEmployees",this.date);
    },
    async getDTRWithoutTriage() {
      await this.$store.dispatch("getDTRWithoutTriage");
    },
    async getHistorical() {
      this.isDetailsShown = true;
      await this.$store.dispatch("getTriageHistorical");
    },
    async getEtriageCIF() {
      this.isEtriageCIFShown = true;
      await this.$store.dispatch("getEtriageCIF");
    },
  },
};
</script>

<style scoped></style>
